import React, { useState } from "react";
import Team from "./Team";
import Individual from "./Individual";
import {
  Divider,
  Grid,
  Button,
  Feed,
  Item,
  Segment
} from "semantic-ui-react";
import { Link } from "react-router-dom";
import moment from "moment";

import img from "../jenny.jpg"
export default function Switcher(props) {
  const { players, teams } = props;
  const [activity, setActivity] = useState([
    { date: moment().calendar(), message: `Game has been started.` }
  ]);
  const changeActivity = newActivity => {
    setActivity(prevActivity => [...prevActivity, newActivity]);
  };
  return (
    <div>
      <Button as={Link} to="/">
        Back to Home
      </Button>
      <Segment basic>
        {Number(props.match.params.mode) === 1 ? (
          <Grid doubling columns="equal">
            <Grid.Row>
              {teams.map(x => (
                <Grid.Column key={x.id}>
                  <Item>
                    <Item.Content verticalAlign="middle">
                      <Item.Image
                        size="tiny"
                        src={img}                      />
                      <Team data={x} method={changeActivity} />
                    </Item.Content>
                  </Item>
                </Grid.Column>
              ))}
            </Grid.Row>
          </Grid>
        ) : (
          <Grid doubling={players.length !== 2} columns={players.length}>
            <Grid.Row>
              {players.map(x => (
                <Grid.Column key={x.id}>
                  <Item>
                    <Item.Content verticalAlign="middle">
                      <Item.Image
                        size="tiny"
                        src={img}
                      />
                      <Individual data={x} method={changeActivity} />
                    </Item.Content>
                  </Item>
                </Grid.Column>
              ))}
            </Grid.Row>
          </Grid>
        )}
        <Divider vertical>VS</Divider>
      </Segment>
      <Segment textAlign="center">
        <Feed>
          {activity.length
            ? activity.map((x, i) => (
                <Feed.Event
                  date={moment().calendar()}
                  image={img}
                  summary={x.message}
                  key={i}
                />
              ))
            : "No activity"}
        </Feed>
      </Segment>
    </div>
  );
}
