import React, { useState } from "react";
import { Button, Input, Item, Container } from "semantic-ui-react";
import { Link } from "react-router-dom";
import img from "../jenny.jpg"

export function Player(props) {
  const [name, setName] = useState(props.name);

  return (
    <Input
      placeholder="Player Name"
      value={name}
      onChange={e => {
        setName(e.target.value);
        props.method(props.id, e.target.value);
      }}
    />
  );
}

export default function Players(props) {
  return (
    <Container>
      <Button as={Link} to="/">
        Back to Home
      </Button>

      <Button onClick={() => props.data.addPlayer()}>Add</Button>

      <Item.Group as={Container} text relaxed divided>
        {props.data.players.map(({ id, name, score }, i) => (
          <Item key={id}>
            <Item.Image
              size="tiny"
              src={img}
            />

            <Item.Content verticalAlign="middle">
              <Player name={name} id={id} method={props.data.changePlayer} />

              <Button
                floated="right"
                onClick={() => props.data.removePlayer(id)}
                disabled={props.data.players.length === 2}
              >
                Remove
              </Button>
            </Item.Content>
          </Item>
        ))}
      </Item.Group>

      <Button primary as={Link} to={`/mode/${props.data.players.length}`}>
        OK
      </Button>
    </Container>
  );
}
