import React from "react";
import { Button } from "semantic-ui-react";
import { Link } from "react-router-dom";

export default function Home(props) {
  return (
    <Button.Group size="large">
      <Button as={Link} to="/Players">
        Players
      </Button>
      <Button.Or />
      <Button as={Link} to="/Teams">
        Teams
      </Button>
    </Button.Group>
  );
}
