import React from "react";
import ReactDOM from "react-dom";
import Switcher from "./components/Switcher";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Players from "./routes/Players";
import Teams from "./routes/Teams";
import useStore from "./store";
import "semantic-ui-css/semantic.min.css";
import Home from "./routes/Home";
import { Segment } from "semantic-ui-react";


function App() {
  const [dataPlayers, dataTeams, players, teams] = useStore();

  return (
    <Router>
      <Segment basic textAlign="center">
        <Route path="/" exact render={props => <Home {...props} />} />
        <Route
          path="/Players"
          exact
          render={props => <Players {...props} data={dataPlayers} />}
        />
        <Route
          path="/Teams"
          exact
          render={props => <Teams {...props} data={dataTeams} />}
        />

        <Route
          path="/mode/:mode"
          exact
          render={props => (
            <Switcher {...props} players={players} teams={teams} />
          )}
        />
        {/* <Route path="/4Players" component={About} />
        <Route path="/3Players" component={Users} />
        <Route path="/2Players" component={Users} /> */}
      </Segment>
    </Router>
  );
}


ReactDOM.render(<App />, document.getElementById('root'));

