import React, { useState } from "react";
import { Header, Button, Input } from "semantic-ui-react";
import moment from "moment";

export default function Team(props) {
  const [count, setCount] = useState(props.data.score);
  const [points, setPoints] = useState(0);

  return (
    <div>
      <Header as="h1">{props.data.name}</Header>

      <Header as="h1">{count}</Header>
      <div>
        <Input
          placeholder="Points won"
          type="number"
          fluid
          value={points === 0 ? "" : points}
          onChange={e => setPoints(Number(e.target.value))}
        />
      </div>
      <Button
        onClick={() => {
          setCount(prevCount => prevCount + points);
          props.method({
            date: moment().calendar(),
            message: `added ${points} to ${props.data.name}`
          });
        }}
      >
        Add
      </Button>
    </div>
  );
}
