import React, { useState } from "react";
import { Button, Input, Item, Container } from "semantic-ui-react";
import { Link } from "react-router-dom";
import img from "../jenny.jpg"

export function Team(props) {
  const [name, setName] = useState(props.name);

  return (
    <Input
      placeholder="Team Name"
      value={name}
      onChange={e => {
        setName(e.target.value);
        props.method(props.id, e.target.value);
      }}
    />
  );
}

export default function Teams(props) {
  return (
    <Container>
      <Button as={Link} to="/">
        Back to Home
      </Button>
      <Item.Group as={Container} text relaxed divided>
        {props.data.teams.map(({ id, name, score }, i) => (
          <Item key={id}>
            <Item.Image
              size="tiny"
              src={img}
            />

            <Item.Content verticalAlign="middle">
              <Team name={name} id={id} method={props.data.changeTeam} />
            </Item.Content>
          </Item>
        ))}
      </Item.Group>
      <Button primary as={Link} to="/mode/1">
        OK
      </Button>
    </Container>
  );
}
